export default {
  install: function(Vue) {
    Vue.prototype.$helpers = {
      chart: {
        d3: {},
        currentData: {},
        margin:{},
        width:0,
        height:0,
        xScale:undefined,
        yScale:undefined,
        xAxis:undefined,
        yAxis:undefined,
	zoom:undefined,
        /* Helper Function */
        addChart: function(d3, ds, options) {
          this.d3=d3;
          this.margin=options.margin;
          this.width=options.width;
          this.height=options.height;

          const thisYearStartDate = options.beginDate;
          const nextYearStartDate = options.endDate;
          // remove invalid data points
          const validData = ds['quote'].filter(
            row => row['high'] && row['low'] && row['close'] && row['open']
          );
          // filter out data based on time period
          this.currentData = validData.filter(row => {
            if (row['date']) {
              return (
                row['date'] >= thisYearStartDate && row['date'] < nextYearStartDate
              );
            }
          });
          // find data range
          const xMin = this.d3.min(this.currentData, d => d['date']);
          const xMax = this.d3.max(this.currentData, d => d['date']);
          const yMin = this.d3.min(this.currentData, d => d['close']);
          const yMax = this.d3.max(this.currentData, d => d['close']);
          // scale using range
          this.xScale = this.d3
            .scaleTime()
            .domain([xMin, xMax])
            .range([0, this.width]);

          this.yScale = this.d3
            .scaleLinear()
            .domain([yMin - 5, yMax + 4])
            .range([this.height, 0]);

          const svg = this.d3
            .select('#chart')
            .append('svg')
            .attr('width', this.width + this.margin['left'] + this.margin['right'])
            .attr('height', this.height + this.margin['top'] + this.margin['bottom'])
            .append('g')
            .attr('transform',`translate(${this.margin['left']}, ${this.margin['top']})`);

          // create the axes component
          this.xAxis = svg
            .append('g')
            .attr('class', 'xAxis')
            .attr('transform', `translate(0, ${this.height})`)
            .call(this.d3.axisBottom(this.xScale));

          this.yAxis = svg
            .append('g')
            .attr('class', 'yAxis')
            .attr('transform', `translate(${this.width}, 0)`)
            .call(this.d3.axisRight(this.yScale));
          svg
            .append('g')
            .attr('id', 'leftAxis')
            .attr('transform', `translate(0, 0)`);

          this.addCrosshair(svg);
          // candlesticks, and with clip-path attribute
          svg
            .append('g')
            .attr('id', 'ohlc-series')
            .attr('clip-path', 'url(#clip)');

          svg
            .exit().remove(); 

          this.updateChart();
        },
	addCrosshair(svg){
          // define x and y crosshair properties
          const focus = svg
           .append('g')
           .attr('class', 'focus')
           .style('display', 'none');

          focus.append('circle').attr('r', 4.5);
          focus.append('line').classed('x', true);
          focus.append('line').classed('y', true);

         svg
           .append('rect')
           .attr('class', 'overlay')
           .attr('width', this.width)
           .attr('height', this.height);

         this.d3.select('.overlay')
           .style('fill', 'none')
           .style('pointer-events', 'all');

         this.d3.selectAll('.focus line')
           .style('fill', 'none')
           .style('stroke', '#67809f')
           .style('stroke-width', '1.5px')
           .style('stroke-dasharray', '3 3');
	},
        updateChart() {
          /* Update the axes */
          this.d3.select('.xAxis').call(this.d3.axisBottom(this.xScale));
          this.d3.select('.yAxis').call(this.d3.axisRight(this.yScale));
          /* updating of crosshair */
          this.updateCrosshairProperties();
          this.addOhlcBars();
        },
        addOhlcBars(){
          const tickWidth = 3;
          const ohlcLine = this.d3
            .line()
            .x(d => d['x'])
            .y(d => d['y']);

          const ohlcSelection = this.d3
            .select('#ohlc-series')
            .selectAll('.ohlc')
            .data(this.currentData, d => d['volume']);

          ohlcSelection.join(enter => {
            const ohlcEnter = enter
            .append('g')
            .attr('class', 'ohlc')
            .append('g')
            .attr('class', 'bars')
            .classed('up-bar', d => d['close'] > d['open'])
            .classed('down-bar', d => d['close'] <= d['open']);
          ohlcEnter
            .append('path')
            .classed('high-low', true)
            .attr('d', d => {
              return ohlcLine([
                { x: this.xScale(d['date']), y: this.yScale(d['high']) },
                { x: this.xScale(d['date']), y: this.yScale(d['low']) }
              ]);
            });
          ohlcEnter
            .append('path')
            .classed('open-tick', true)
            .attr('d', d => {
              return ohlcLine([
                {
                  x: this.xScale(d['date']) - tickWidth,
                  y: this.yScale(d['open'])
                },
                { x: this.xScale(d['date']), y: this.yScale(d['open']) }
              ]);
            });
          ohlcEnter
            .append('path')
            .classed('close-tick', true)
            .attr('d', d => {
              return ohlcLine([
                { x: this.xScale(d['date']), y: this.yScale(d['close']) },
                {
                  x: this.xScale(d['date']) + tickWidth,
                  y: this.yScale(d['close'])
                }
              ]);
            });
          });
        },
        updateCrosshairProperties() {
          // select the existing crosshair, and bind new data
          const overlay = this.d3.select('.overlay');
          const focus = this.d3.select('.focus');

          // remove old crosshair
          overlay.exit().remove();

          // enter, and update the attributes
          overlay
            .enter()
            .append('g')
            .attr('class', 'focus')
            .style('display', 'none');

          overlay
            .attr('class', 'overlay')
            .attr('width', this.width)
            .attr('height', this.height)
            .on('mouseover', () => focus.style('display', null))
            .on('mouseout', () => focus.style('display', 'none'))
            .on('mousemove', (d, i, nodes) => this.generateCrosshair(nodes[i]));
        },
        /* Mouseover function to generate crosshair */
        generateCrosshair(current) {
          //returns corresponding value from the domain
          const focus = this.d3.select('.focus');
          const bisectDate = this.d3.bisector(d => d.date).left;
          const correspondingDate = this.xScale.invert(this.d3.mouse(current)[0]);
          //gets insertion point
          const i = bisectDate(this.currentData, correspondingDate, 1);
          const d0 = this.currentData[i - 1];
          const d1 = this.currentData[i];
          const currentPoint =
            correspondingDate - d0['date'] > d1['date'] - correspondingDate ? d1 : d0;
          focus.attr(
            'transform',
            `translate(${this.xScale(currentPoint['date'])}, ${this.yScale(currentPoint['close'])})`);

          focus
            .select('line.x')
            .attr('x1', 0)
            .attr('x2', this.width - this.xScale(currentPoint['date']))
            .attr('y1', 0)
            .attr('y2', 0);

          focus
            .select('line.y')
            .attr('x1', 0)
            .attr('x2', 0)
            .attr('y1', 0)
            .attr('y2', this.height - this.yScale(currentPoint['close']));

        }
      }
    }
  }
}
