<template>
  <div class="flex-item">
      <div id="chart"></div>
  </div>
</template>
<script>
export default {
  name: 'Chart',
  data() {
    return {
      options: {
	margin:{ top: 50, right: 50, bottom: 50, left: 20 },
        height:0,
        width:0
      }
    }
  },
  mounted: function() {
    this.renderCharts();
  },
  beforeUpdate: function(){
    this.renderCharts();
  },
  methods: {
     renderCharts: function (){
      const margin=this.options.margin;	
      const viewportWidth = Math.max(
        document.documentElement.clientWidth,
        window.innerWidth
      );
      const viewportHeight = Math.max(
        document.documentElement.clientHeight,
        window.innerHeight
      );
      let w,h;
      if (viewportWidth <= 768) {
        w = viewportWidth - margin.left - margin.right; // Use the window's width
        h = 0.5 * viewportHeight - margin.top - margin.bottom; // Use the window's height
      } else {
        w = 0.75 * viewportWidth - margin.left - margin.right;
        h = viewportHeight - margin.top - margin.bottom; // Use the window's height
      }
      this.options.width=w;
      this.options.height=h;
      this.options.selector = '#chart';
      this.options.title = "Bar Chart!";
      this.options.beginDate = new Date(2018, 4, 31);
      this.options.endDate = new Date(2019, 1,1);

      const loadFile = 'sample-data-vea.json';
    

      this.$d3.json(loadFile).then(data => {
        const chartResultsData = data['chart']['result'][0];
        const quoteData = chartResultsData['indicators']['quote'][0];

        return {
          dividends: Object.values(chartResultsData['events']['dividends']).map(
          res => {
              return {
                date: new Date(res['date'] * 1000),
                yield: res['amount']
              };
            }
          ),
          quote: chartResultsData['timestamp'].map((time, index) => ({
            date: new Date(time * 1000),
            high: quoteData['high'][index],
            low: quoteData['low'][index],
            open: quoteData['open'][index],
            close: quoteData['close'][index],
            volume: quoteData['volume'][index]
          }))
        };
      }).then(response=>{
        this.$helpers.chart.addChart(this.$d3, response, this.options);
      });

     }
  }
}
</script>
<style>
#chart {
  background: #0e3040;
  color: #67809f;
  width: 100%;
  height: 100%;
}

/* chart styling */
a {
  color: #67809f;
}

.tooltip {
  position: absolute;
  text-align: center;
  width: 150px;
  height: 38px;
  padding: 2px;
  border-radius: 5px;
  font: 12px sans-serif;
  background: lightsteelblue;
  pointer-events: auto;
}

.tooltip:after {
  box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 0;
  color: lightsteelblue;
  content: "\25BC";
  position: absolute;
  text-align: center;
  top: 100%;
  left: 0;
}

.bars.up-bar path {
  stroke: #03a678;
}

.bars.down-bar path {
  stroke: #c0392b;
}

.bars.up-bar rect {
  fill: #03a678;
}

.bars.down-bar rect {
  fill: #c0392b;
}

</style>

